import React, { useEffect } from 'react'

import Carouselimg from './Carousel';

import Mytab from './Mytab';
import { connect, Provider } from 'react-redux';
import store from './store';
import { getList, login } from './actionCreators';
import { SearchBar } from 'antd-mobile';
//首页
const Home = (props) => {
    useEffect(() => {
        store.dispatch(getList())
    }, [])


    return  <div style={{ marginBottom: '50px' }}>


        <div
            style={{ height: '40px', width: '100%', position: 'fixed', top: '0', backgroundColor: 'rgb(255,78,34)', zIndex: '1' }}
            onClick={() => {
                props.history.push('/details')

            }}
        >
        
            <SearchBar
                placeholder="请输入宝贝店铺"//默认搜索框的文字
                maxLength={16}//最大输入长度
               style={{
                   width:'85%',
                   height:'26px',
                   backgroundColor:'rgb(255,78,34)',
                   marginLeft:'8%',
                   marginTop:'6px'
               }}
                />
            {/* <input placeholder="请输入搜索店铺" style={{ marginLeft:'10%',marginTop:'5px',height: '25px', width: '80%', backgroundColor: 'rgb(255,78,34)', }} /> */}
            </div>


        {/* 轮播图 */}
        <Carouselimg />
        {/* 页面商品列表 */}
        <div style={{ width: '100%' }}>
            <div style={{ marginTop: '20px', marginLeft: '30%', fontSize: '1.25em', width: '56%'}}>~~~~猜你喜欢~~~~</div>
            {
                Array.from(props.listArr).map((item, idx) => {
                    return <li
                        style={{
                            listStyle: 'none',
                            display: 'inline-block',
                            width: '40%',
                            height: '180px',
                            margin: '4.5%',
                            key: idx,
                            backgroundColor: 'white',
                            borderRadius: '10%'
                        }}
                        onClick={() => {
                            props.history.push({ pathname: '/child', index: `${idx}`, price: `${item.price}`, title: `${item.title}` });
                            // 传递跳转页面，图片下标
                            // props.location.

                        }}

                    >
                        <img style={{
                            width: '100%',
                            height: '135px',
                            borderRadius: '10%'
                        }} src={`${item.img}`} key={`${idx}`} />
                        <div style={{ fontSize: '0.8rem', paddingLeft: '15px' }}><span style={{ color: 'red', fontSize: '1.0rem' }}>￥{`${item.price}`}</span><br />{`${item.title}`}</div>
                    </li>


                })
            }
        </div>
        <Mytab />

        </div>
    


        
}

const mapStateToProps = (state) => {
    return {
        listArr: state.list
    }
}

export default connect(mapStateToProps)(Home)

