import { Button } from 'antd-mobile'
import React from 'react'
import { connect } from 'react-redux'
import {login} from './actionCreators'
// Button
const login1 = (props) => {
const log=()=>{
   
        props.dispatch(login())
 
}
  console.log(Boolean(props.location.state));
    return (
        <div>
      {props.username ?
                <div>
                   {/* <alert>登陆成功</alert> */}
                    {props.username}
                    {props.location.state?
                   <div>{props.history.push({pathname:'/child',index:`${props.location.state}`})}</div>:
                   <div>{props.history.push({pathname:'/shopping'})}</div>}
                    
                    {/* <button onClick={ ()=>{
                        props.dispatch({
                            type:'LOGIN',
                            username:''
                        })
                    }}>退出</button> */}
                </div>
                : <div>
                <input
                placeholder={'请输入用户名'}
                style={{
                width:'80%',
                height:'30px',
                marginBottom:'15px',
                marginTop:'200px',
                marginLeft:'10%',
                borderRadius:'12px'
                }}
                 ></input>
                 <input
                 type={'password'}
                  style={{
                    width:'80%',
                    height:'30px',
                    marginBottom:'15px',
                    marginLeft:'10%',
                    borderRadius:'12px'
                    }}
                placeholder={'请输入密码'} 
                 >

                 </input>
                <button onClick={log}
                style={{
                    width:'60%',
                    height:'30px',
                    marginBottom:'5px',
                    marginLeft:'18%',
                    borderRadius:'12px'
                    }}
                >登录</button>
                </div>
            }
                {/* <button onClick={()=>{props.history.push({pathname:'/child',index:`${props.location.state}`})}}>返回</button> */}
                </div>
                
            
    )
}

const mapStateToProps = (state) => {
    return {
        username: state.name.userinfo
    }
}

export default connect(mapStateToProps)(login1)
