import React, { Component } from 'react'
import { Carousel, WingBlank } from 'antd-mobile';

// 轮播图组件
export class Carouselimg extends Component {
  state = {
    data: ['https://gw.alicdn.com/bao/uploaded/i3/865087109/O1CN01L4DVUq22Ny8eSb9jK_!!865087109.jpg_500x500q90.jpg_.webp',
    'https://gw.alicdn.com/bao/uploaded/i2/36436176/TB23cD2dkfb_uJjSsD4XXaqiFXa_!!36436176.jpg_500x500q90.jpg_.webp',
     'https://gw.alicdn.com/bao/uploaded/i1/TB1Td08OpXXXXaOXpXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/bao/uploaded/i3/865087109/O1CN01L4DVUq22Ny8eSb9jK_!!865087109.jpg_500x500q90.jpg_.webp',
         'https://gw.alicdn.com/bao/uploaded/i2/36436176/TB23cD2dkfb_uJjSsD4XXaqiFXa_!!36436176.jpg_500x500q90.jpg_.webp',
          'https://gw.alicdn.com/bao/uploaded/i1/TB1Td08OpXXXXaOXpXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp'],
      });
    }, 100);
  }
  render() {
    return <div  >
      {/* <WingBlank > */}
      <Carousel
        autoplay={true}//轮播图是否自动播放
        infinite={true}//是否循环
        // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}//循环的前一张图，滑到后一张
        // afterChange={index => console.log('slide to', index)}//下一张图
        style={{ marginTop: '40px' }}
      >
        {this.state.data.map(val => (
          // 轮播图链接的网页地址
          <a
            key={val}
            href="http://www.taobao.com"
            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
          >
            {/* 轮播图图片 */}
            <img
              src={`${val}`}
              alt=""
              style={{ width: '100%', verticalAlign: 'top' }}
              onLoad={() => {
                // fire window resize event to change height，当变成网页时改变轮播图图片高度
                window.dispatchEvent(new Event('resize'));
                // this.setState({ imgHeight: 'auto' });
              }}
            />
          </a>
        ))}
      </Carousel>
      {/* </WingBlank> */}
    </div>
  }

}

export default Carouselimg
