import React, { useEffect } from 'react'
import { NavLink, Route, Redirect, useHistory, useLocation, useParams, useRouteMatch, withRouter } from 'react-router-dom'
import { Button, Icon, NavBar } from 'antd-mobile';
// import listArr from './li.list';
import Tab from './Tab';
import { connect } from 'react-redux';
// import one from './image/one.png'
// listArr

{/* 商品详情页 */ }
const Child = (props) => {
    console.log( props);
    //获取图片下标
    return <div>


        <li
            style={{
                listStyle: 'none',
                display: 'inline-block',
                width: '100%',
                height: '300px',

            }}
        >
            <div style={{ position: 'absolute' }}><button
                style={{ backgroundColor: 'grey', opacity: '1', width: '40px', height: '40px', borderRadius: '50%', opacity: '0.5', marginLeft: '20px', marginTop: '20px' }}
                mode="light"
                onClick={() => props.history.push('./home')}
            >＜</button></div>
            <div style={{ position: 'relative', zIndex: '-1' }}><img style={{ width: '100%', height: '300px', }} src={props.listArr[props.location.index].img} /></div>
            <div  style={{fontSize:'1.1rem'}}> <div><span style={{color:'red',fontSize:'1.5rem'}}>￥{props.listArr[props.location.index].price}</span><br/>{props.listArr[props.location.index].title }</div></div>
        </li>
        <div style={{
            position: 'fixed',
            bottom: 0,
            width: '100%',
            height: '30px',
            backgroundColor: 'rgb(230,230,250)'
        }}>
            <Tab idx={props.location.index} />
        </div>

    </div>
}

const mapStateToProps = (state) => {
    return {
        listArr:state.list
    }
}

export default connect(mapStateToProps)(Child)
