import React from 'react'
import { connect } from 'react-redux'
import Mytab from './Mytab'

const Shopping = (props) => {
    console.log(props);
    return (
        <div>
            {Array.from(props.id).map((item,idx) => {
                // console.log(item)
                    return <li
                    key={idx}
                        style={{
                            listStyle: 'none',
                            display: 'inline-block',
                            width: '40%',
                            height: '180px',
                            margin: '4.5%',
                            key: idx,
                            backgroundColor: 'white',
                            borderRadius: '10%'
                        }}
                        // onClick={() => {
                            // props.history.push({ pathname: '/child', index: `${item}`, price: `${props.listArr[item].price}`, title: `${props.listArr[item].title}` });
                            // 传递跳转页面，图片下标
                            // props.location.

                        // }}

                    >
                        <img style={{
                            width: '100%',
                            height: '135px',
                            borderRadius: '10%'
                        }} src={`${props.listArr[item].img}`} key={`${idx}`} />
                        <div style={{ fontSize: '0.8rem' ,paddingLeft:'15px'}}><span style={{ color: 'red', fontSize: '1.0rem' }}>￥{`${props.listArr[item].price}`}</span><br />{`${props.listArr[item].title}`}</div>
                        <button   onClick={()=>{
                           
                            props.dispatch({
                                type:'DELETE',
                                index:idx
                            })
                        }}  >删除</button>
                    </li>
            })}

            <Mytab />
            
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        listArr:state.list,
        id:state.shoucang

    }
}

export default connect(mapStateToProps)(Shopping)

