import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from './actionCreators';
//底部固定栏
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
            hidden: false,
            fullScreen: false,
        };
    }



    render() {
        const pathname = this.props.location.pathname;
        // console.log(this.props);

        return (
            <div style={{
                position: 'fixed',
                bottom: 0,
                width: '100%',
            }} >

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#EE2C2C"
                    barTintColor="white"
                    hidden={false}
                >
                    {/* 每个页面以及里面的效果 */}
                    <TabBar.Item
                        title="首页"
                        key="Life"
                        icon={<i className="iconfont iconhome-o"></i>
                        }
                        selectedIcon={<i className="iconfont iconshouye-yes"></i>

                        }
                        // 匹配子路由
                        selected={pathname === '/home'}

                        onPress={() => {
                            this.props.history.push('/home')
                        }}
                        data-seed="logId"
                    >


                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className="iconfont  iconshoucang"></i>
                        }
                        selectedIcon={
                            <i className="iconfont iconshoucang2"></i>
                        }
                        title="收藏"
                        key="Koubei"

                        selected={pathname === '/shopping'}
                        onPress={() => {
                            this.props.username ? this.props.history.push('/shopping') : this.props.history.push('/login')
                        }


                        }
                        data-seed="logId1"
                    >

                    </TabBar.Item>
                    {/* 每个页面以及里面的效果 */}

                    <TabBar.Item
                        icon={
                            <i className="iconfont icondingdanliebiao"></i>
                        }
                        selectedIcon={
                            <i className="iconfont  icondingdanliebiao_s"></i>
                        }
                        title="订单列表"
                        key="Friend"
                        // dot
                        selected={pathname === '/list'}
                        onPress={() => {

                            this.props.history.push('/list')

                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont  iconme"></i>}
                        selectedIcon={<i className="iconfont  iconIcon_wodeB"></i>}
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/personal'}
                        onPress={() => {
                            this.props.history.push('/personal')

                        }}
                    >

                    </TabBar.Item>

                    <TabBar.Item
                        icon={<i className="iconfont  icongengduo1"></i>}
                        selectedIcon={<i className="iconfont  icongengduo"></i>}
                        title="更多"
                        key="my"
                        selected={pathname === '/more'}
                        onPress={() => {

                            this.props.history.push('/more')

                        }}
                    >

                    </TabBar.Item>




                </TabBar>



            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        username: state.name.userinfo
    }
}

export default connect(mapStateToProps)(withRouter(Mytab))
